.myWorksBlock{
    height: 100vh;
    background-color: #1f1f20;
}

.myWorksContainer{
    flex-direction: column;
    justify-content: center;
}

.works{
    border: 1px solid;
    margin-top: 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
}

@media screen and (max-width: 1000px) {
    .works {
        flex-direction: column;
        align-items: center;
    }
}
